header {
    position: relative;
    height: 50px; }
nav {
    position: fixed;
    width: 100%;
    height: 50px;
    background-color: #212121;
    z-index: 2; }
.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    &_link {
        color: #ffffff;
        font-size: 15px;
        font-weight: 300;
        transition: 0.5s all;
        &::after {
            content: '';
            display: block;
            height: 2px;
            width: 0%;
            background-color: red;
            transition: 0.5s all; }
        &:hover {
            &::after {
                width: 100%; } } }

    &_item {
        border-right: 2px dotted black;
        padding-right: 28px;
        &:last-child {
            border-right: none; } } }

.hamburger {
    display: none; }

.subheader {
    position: absolute;
    top: 50px;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    &_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }
    &_official {
        margin-top: 6px;
        opacity: 0.5;
        color: #ffffff;
        font-size: 13px;
        font-weight: 400; }
    &_call {
        color: #ffffff;
        font-size: 12px;
        font-weight: 300;
        margin-top: 19px;
        text-align: right; }
    &_phone {
        display: block;
        margin-top: 8px;
        color: #ffffff;
        font-size: 22px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: right;
        line-height: 12px; }
    &_btn {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, $mine-color 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        @include btn_hover; } }
.promo {
    min-height: 800px;
    background: url('../img/bg/uber_bg.jpg') center (center / cover) no-repeat;
    padding: 150px 0 135px 0;
    &_header {
        color: #ffffff;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 60px;
        line-height: 60px;
        margin: 0;
        text-align: center; }
    &_subheader {
        color: #ffffff;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 36px;
        line-height: 36px;
        margin: 0;
        margin-top: 22px;
        text-align: center; }
    &_descr {
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        text-align: center;
        margin-top: 23px; }
    &_btn {
        display: block;
        margin: 118px auto 0 auto;
        width: 259px;
        height: 63px;
        box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, $mine-color 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase;
        @include btn_hover; } }
.label {
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center; }
.title {
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center; }
.subtitle {
    margin-top: 19px;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    text-align: center;
    color: #222222; }

.advantages {
    padding: 71px 0 65px 0;
    &_item {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        margin-top: 35px; }
    &_dot {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 26px;
        width: 113px;
        height: 113px;
        background-color: $mine-color;
        border-radius: 100%; }
    &_box {
        width: 343px; }
    &_top {
        line-height: 21px;
        color: $mine-color;
        font-size: 17px;
        font-weight: 400; }
    &_down {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px; } }
.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/bg/mobile_bg.jpg') center (center / cover) no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url('../img/Choice/car_1.jpg') center (center / cover) no-repeat; }
        &_2 {
            background: url('../img/Choice/car_2.jpg') center (center / cover) no-repeat; }
        &_3 {
            background: url('../img/Choice/car_3.jpg') center (center / cover) no-repeat; }
        &_4 {
            background: url('../img/Choice/car_4.jpg') center (center / cover) no-repeat; }
        &_5 {
            background: url('../img/Choice/car_5.jpg') center (center / cover) no-repeat; }
        &_6 {
            background: url('../img/Choice/car_6.jpg') center (center / cover) no-repeat; }
        &_subtitle {
            font-weight: normal;
            font-size: 24px;
            line-height: 31px;
            text-align: center;
            text-transform: uppercase;
            color: #FFFFFF; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            margin: 0 auto;
            width: 26px;
            height: 26px;
            background-color: #000;
            border-radius: 100%;
            cursor: pointer;
            &::before {
                content: '';
                display: block;
                width: 14px;
                height: 2px;
                background-color: $mine-color;
                transition: 0.3s all; }
            &::after {
                content: '';
                display: block;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                width: 2px;
                height: 14px;
                background-color: $mine-color;
                transition: 0.3s all; }
            &:hover {
                &::before, &::after {
                    background-color: $color_2; } } } } }
.lable_white {
  background-color: #fff;
  color: #000; }
.title_white {
  color: #fff; }
.subtitle_white {
  color: #fff; }
.opportunities {
    position: relative;
    height: 499px;
    background-color: #000;
    &_blak {
        width: 100%;
        padding-top: 48px;
        background-color: #000; }
    &_img {
        position: absolute;
        width: 759px;
        height: 480px;
        top: 279px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1; } }
.opportunities2 {
    position: relative;
    min-height: 397px;
    background: url('../img/bg/mobile_bg.jpg') center (center / cover) no-repeat;
    z-index: -1;
    &_item {
        position: absolute;
        top: 259px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 2;
        margin: 0;
        font-weight: 300;
        font-size: 17px;
        line-height: 24px;
        color: #222222; } }
.require {
    padding: 68px 0 84px 0;
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0; } }
    &_title {
        font-weight: normal;
        font-size: 22px;
        line-height: 24px;
        color: #1CA8C3; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            position: relative;
            margin-bottom: 10px;
            font-weight: 300;
            font-size: 17px;
            line-height: 24px;
            color: #222222;
            &::before {
                content: '';
                display: block;
                position: absolute;
                left: -32px;
                top: 3px;
                width: 21px;
                height: 20px;
                background: url('../icons/check_blue.svg') center (center / cover) no-repeat; } } }
    &_descr {
        font-weight: 300;
        font-size: 17px;
        line-height: 24px;
        color: $mine-color; }
    .warning {
        .require_title {
            color: $color_2; }
        .require_list {
            li {
                &::before {
                    background: url('../icons/check_red.svg') center (center / cover) no-repeat; } } } } }
.world {
    position: relative;
    min-height: 717px;
    padding: 49px 0;
    background: url('../img/bg/world_bg.jpg') center (center / cover) no-repeat;
    &_link {
        display: block;
        position: absolute;
        bottom: 50px;
        left: 50%;
        transform: translateX(-50%);
        font-size: 15px;
        text-align: center;
        text-transform: uppercase;
        color: #1FBAD6;
        &:hover {
            color: #fff; } } }
.footer {
    position: relative;
    height: 155px;
    padding-top: 11px;
    &_line {
        width: 300px;
        height: 7px;
        margin: 0 auto;
        background-color: $mine-color; }
    &_links {
        display: block;
        margin: 0 auto;
        text-align: center;
        align-items: center;
        margin-top: 35px; }
    &_link {
        margin-right: 35px;
        font-weight: 300;
        font-size: 15px;
        text-transform: uppercase;
        color: #070716;
        &:nth-child(3) {
            margin-right: 0; } }

    &_sublinks {
        display: block;
        margin: 0 auto;
        margin-left: 20px;
        text-align: center;
        align-items: center;
        margin-top: 16px; }
    &_sublink {
        margin-right: 23px;
        font-weight: 300;
        font-size: 11px;
        text-transform: uppercase;
        color: #999999;
        &:nth-child(3) {
            margin-right: 0; } }
    &_icons {
        display: block;
        position: absolute;
        left: 23px;
        top: 50%;
        transform: translateY(-50%); }
    &_fb {
        display: inline-block;
        background: url('../icons/facebook.svg') center (center / 50%) no-repeat;
        @include ftricn; }
    &_tw {
        display: inline-block;
        background: url('../icons/twitter.svg') center (center / 50%) no-repeat;
        @include ftricn; }
    &_lin {
        display: inline-block;
        background: url('../icons/linkedin.svg') center (center / 50%) no-repeat;
        @include ftricn; }
    &_inst {
        display: inline-block;
        background: url('../icons/instagram.svg') center (center / 50%) no-repeat;
        @include ftricn; }
    &_store {
        display: block;
        position: absolute;
        right: 21px;
        top: 50%;
        transform: translateY(-50%); }
    &_apple {
        display: inline-block;
        background: url('../icons/store/apple.png') center (center / cover) no-repeat;
        @include store; }
    &_google {
        display: inline-block;
        background: url('../icons/store/googleapp.png') center (center / cover) no-repeat;
        @include store; }
    &_windows {
        display: inline-block;
        background: url('../icons/store/windows.png') center (center / cover) no-repeat;
        @include store; } }

.xs-visible {
    display: none; }
.xs-opportunities_sub {
    display: none; }
